@import url(https://fonts.googleapis.com/css2?family=Recursive&display=swap);
.recipe_recipe__23h-N {
	border-radius: 10px;
	box-shadow: 0px 5px 20px rgb(71, 71, 71);
	margin: 20px;
	padding: 15px;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	flex-grow: 1;
	background: white;
	align-items: center;
	min-width: 20%;
	background-image: linear-gradient(135deg, #e2d1c3 20%, #fdfcfb 70%);
}

.recipe_list__3A2BW {
	width: 40%;
	font-size: 16px;
}

.recipe_image__1N-99 {
	padding: 5px;
	border-radius: 50%;
	width: 150px;
	height: 150px;
}

.recipe_kcal__2-iK7 {
	font-size: 35px;
	font-weight: bold;
}

@media screen and (max-width: 768px) {
	.recipe_recipe__23h-N {
		flex-direction: column;
		justify-content: space-between;
		align-items: center;
		min-width: 20%;
	}
	.recipe_header__1qB6R {
		margin: 0 auto;
		font-size: 25px;
	}

	.recipe_list__3A2BW {
		width: 90%;
		font-size: 16px;
	}

	.recipe_image__1N-99 {
		width: 100px;
		height: 100px;
	}

	.recipe_kcal__2-iK7 {
		margin: 0 auto;
		font-size: 25px;
	}
}

body {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Recursive", sans-serif;
}
.App {
	min-height: 100vh;
	background-image: linear-gradient(to right, #434343 0%, black 100%);
}

.logo {
	position: absolute;
	top: 0;
	right: 5%;
	width: 90px;
}

.search-form {
	margin: 0 auto;
	min-height: 10vh;
	max-width: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.search-bar {
	font-family: "Recursive", sans-serif;
	font-weight: bold;
	font-size: 15px;
	background-image: linear-gradient(135deg, #e2d1c3 20%, #fdfcfb 70%);
	margin-right: 10px;
	border-radius: 10px;
	width: 50%;
	border: none;
	padding: 10px;
}

.search-button {
	font-family: "Recursive", sans-serif;
	font-size: 15px;
	font-weight: bold;
	border-radius: 10px;
	background: #fdfcfb;
	border: none;
	padding: 10px 20px;
	color: black;
}

.recipes {
	font-size: 15px;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
}

/* MEDIA QUERIES */

@media screen and (max-width: 768px) {
	.search-form {
		max-width: 60%;
	}
	.logo {
		top: 0;
		right: 0;
		width: 70px;
	}
}

